<template>
  <div class="main">
    <div class="nav-list">
      <ul>
        <li v-for="item in departments" class="nav-list-item">
          <router-link :to="item.to" v-if="item.to">
            <span>{{item.name}}</span>
            <em class="icon iconn-5 icon-info"></em>
          </router-link>
          <span v-if="!item.to">{{item.name}}</span>
        </li><!-- 
        <li class="nav-list-item">
          <router-link to="/xmsx/option/1/2">
            <span>大数据中心</span>
            <em class="icon iconn-5 icon-info"></em>
          </router-link>
        </li>
        <li class="nav-list-item">
          <router-link to="/xmsx/option/1/3">
            <span>技术中心</span>
            <em class="icon iconn-5 icon-info"></em>
          </router-link>
        </li>
        <li class="nav-list-item">
          <router-link to="/xmsx/option/1/4">
            <span>架构部</span>
            <em class="icon iconn-5 icon-info"></em>
          </router-link>
        </li>
        <li class="nav-list-item">
          <router-link to="/xmsx/option/1/5">
            <span>商城技术部</span>
            <em class="icon iconn-5 icon-info"></em>
          </router-link>
        </li>
        <li class="nav-list-item">
          <router-link to="/xmsx/option/1/6">
            <span>基础支撑中心</span>
            <em class="icon iconn-5 icon-info"></em>
          </router-link>
        </li> -->
      </ul>
    </div>
    <router-view></router-view>
  </div>
</template>
<script>
  export default {
    name: 'app-lxxx',
    data () {
      return {
        departments: []
      }
    },
    created(){
      var id = this.$route.params.id;
      var arr = [];
      if(id == 1){
        arr = ['全部', '大数据', '技术中心', ' 架构部', ' 商城技术', ' 基础支撑中心'];
      }else if(id == 2){
        arr = ['全部', '在建工程', '战略项目', ' 部门级'];
      }else if(id == 3){
        arr = ['全部', '需求阶段', '设计阶段', ' 开发阶段', ' 测试阶段', ' 上线阶段'];
      }else if(id == 4){
        arr = ['全部', '沟通风险', '资源风险', ' 硬件风险', ' 依赖风险'];
      }else if(id == 5){
        arr = ['全部', '未启动', '正在进行', ' 延期', ' 暂停/取消', ' 部分上线'];
      }
      if(id == 1){
        arr.forEach( (v, i) => {
          this.departments.push({
            name: v,
            to: '/xmsx/option/'+ i +'/1'
          });
        });
      }else{
        arr.forEach( (v, i) => {
          this.departments.push({
            name: v
          });
        });
      }
    }
  }
</script>
<style scoped lang="scss">
@import "../../assets/sass/list.scss";
</style>



