<template>
  <div class="main">
    <div class="nav-list">
      <ul>
        <li v-for="item in departments" class="nav-list-item">
          <router-link to="/xmsx">
            <span>{{item.name}}</span>
            <em class="icon iconn-5 icon-info"></em>
          </router-link>
        </li>
      </ul>
    </div>
    <router-view></router-view>
  </div>
</template>
<script>
  export default {
    name: 'app-zl',
    data () {
      return {
        departments: []
      }
    },
    created(){
      var id = this.$route.params.id;
      var a = ['全部', '大数据', '技术中心', ' 架构部', ' 商城技术', ' 基础支撑中心'];
      console.log(id);
      for(var i = 0; i <= 3; i++){
        this.departments.push({
          name: a[id-1] + '部门:' + i
        });
      }/*
      if(id == 1){
        this.departments = ['大数据部门1', '大数据部门2', '大数据部门3'];
      }else{
        //
      }*/
    }
  }
</script>
<style scoped lang="scss">
@import "../../assets/sass/list.scss";
</style>



